<template>
  <div class="list">
    <div class="banner">
      <div class="bannerImgs">
        <ul>
          <li
            v-for="(item, index) in swiper"
            :key="index"
            ref="li"
            @touchstart="touchstart"
            @touchmove="touchmove"
            @touchend="touchend"
            @click="topNavigate(item, $event)"
          >
            <div class="pic">
              <img :src="item.picUrl ? item.picUrl : item.coverImgUrl" />
              <div class="count">
                <img src="../assets/images/itemplay.png" />
                {{
                  item.playCount > 10000
                    ? item.playCount > 100000000
                      ? (item.playCount / 100000000).toFixed(2) + "亿"
                      : (item.playCount / 10000).toFixed(2) + "万"
                    : item.playCount
                }}
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <ul class="main">
      <ListItem
        v-for="list in lists"
        :key="list.id"
        :list="list"
        @navigate-playList="navigateToPlayList(list.id)"
      ></ListItem>
    </ul>
  </div>
</template>

<script>
import ListItem from "@/components/ListItem.vue";

export default {
  name: "List",
  components: {
    ListItem,
  },
  data() {
    return {
      lists: [],
      swiper: [],
      config: [
        {
          width: 3.2, // 120px -- 3.2rem
          top: 0.26, // 10px -- 0.26rem
          left: 0,
          zIndex: 1,
          opacity: 0.5,
        },
        {
          width: 3.73, // 140px -- 3.73rem
          top: 0,
          left: 2.6, // 100px -- 2.67rem
          zIndex: 3,
          opacity: 1,
        },
        {
          width: 3.2, // 120px -- 3.2rem
          top: 0.26, // 10px -- 0.26rem
          left: 5.3, // 200px -- 5.3rem
          zIndex: 1,
          opacity: 0.5,
        },
      ],
      startX: 0,
      distance: 0,
    };
  },
  watch: {
    swiper() {
      // this.$nextTick(() => {
      //   this.init(this.config);
      // });
    },
  },
  created() {
    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      duration: 0,
    });

    this.axios
      .get("/personalized")
      .then((response) => {
        this.swiper = response.data.result.slice(0, 3);
        this.lists = response.data.result.slice(3);

        this.$nextTick(() => {
          console.log(this.$refs.li);
          this.init(this.config);
        });
        this.$toast.clear();
      })
      .catch(() => {
        this.$toast.clear();
      });
  },
  methods: {
    navigateToPlayList: function (id) {
      this.$router.push({
        name: "Playlist",
        query: {
          id,
        },
      });
    },
    init(myData) {
      myData.forEach((element, index) => {
        this.$refs.li[index].style.width = element.width + "rem";
        this.$refs.li[index].style.top = element.top + "rem";
        this.$refs.li[index].style.left = element.left + "rem";
        this.$refs.li[index].style.zIndex = element.zIndex;
        this.$refs.li[index].style.opacity = element.opacity;
      });
    },
    touchstart(e) {
      this.startX = e.touches[0].clientX;
    },
    touchmove(e) {
      this.distance = Math.floor(e.touches[0].clientX - this.startX);
    },
    touchend() {
      if (this.distance > 70) {
        this.config.push(this.config.shift());
        this.init(this.config);
      } else if (this.distance < -70) {
        this.config.unshift(this.config.pop());
        this.init(this.config);
      }

      this.startX = 0;
      this.distance = 0;
    },
    topNavigate(item, $event) {
      var currentLi = $event.target.parentNode.parentNode;
      if (currentLi.style.opacity == 1) {
        this.navigateToPlayList(item.id);
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.$store.commit("updateActiveid", 1);
    });
  },
};
</script>

<style lang="less" scoped>
.list {
  margin-top: 86px;
  margin-bottom: 50px;
  padding: 8px 0;
  background-color: #fff;
  .main {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  /deep/ .van-swipe {
    display: flex;
    justify-content: center;
    align-items: center;
    .van-swipe-item {
      &:nth-of-type(1) {
        background-color: lightblue;
      }
      &:nth-of-type(2) {
        background-color: lightcoral;
      }
      &:nth-of-type(3) {
        background-color: lightseagreen;
      }
    }
  }
  .banner {
    height: 150px;
    width: 320px;
    margin: 0 auto;
    position: relative;
    .bannerImgs {
      ul li {
        position: absolute;
        opacity: 0.5;
        transition: all 0.3s linear;
        border-radius: 10px;
        overflow: hidden;
        .pic {
          position: relative;
          img {
            display: block;
            width: 100%;
          }
          .count {
            font-size: 12px;
            position: absolute;
            color: #fff;
            top: 0;
            right: 0;
            background-color: rgba(153, 153, 153, 0.5);
            border-radius: 10px;
            padding: 0 6px;
            margin-top: 3px;
            margin-right: 3px;
            display: flex;
            align-items: center;
            img {
              display: inline-block;
              width: 14px;
              margin-right: 2px;
            }
          }
        }
      }
    }
  }
}
</style>