<template>
  <li class="list-item" @click="$emit('navigate-playList')">
    <div class="pic">
      <img :src="list.picUrl ? list.picUrl :list.coverImgUrl" />
      <div class="count">
        <img src="../assets/images/itemplay.png" />
        {{ num }}
      </div>
    </div>
    <div class="info">{{ list.name }}</div>
  </li>
</template>

<script>
export default {
  name: "ListItem",
  props: ["list"],
  computed: {
    num() {
      var n = this.list.playCount;
      if (n > 100000000) {
        n = (n / 100000000).toFixed(2) + "亿";
      } else if (n > 10000) {
        n = (n / 10000).toFixed(2) + "万";
      }
      return n;
    },
  },
  
};
</script>

<style lang="less" scoped>
.list-item {
  width: 30vw;
  box-sizing: border-box;
  margin: 4px 3px;
  border-radius: 8px;
  overflow: hidden;
  .pic {
    position: relative;
    img {
      display: block;
      width: 100%;
    }
    .count {
      font-size: 12px;
      position: absolute;
      color: #fff;
      top: 0;
      right: 0;
      background-color: rgba(153, 153, 153, 0.5);
      border-radius: 10px;
      padding: 0 6px;
      margin-top: 3px;
      margin-right: 3px;
      display: flex;
      align-items: center;
      img {
        display: inline-block;
        width: 14px;
        margin-right: 2px;
      }
    }
  }
  .info {
    font-size: 13px;
    margin-top: 2px;

    word-wrap: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1; /*行数*/
    -webkit-box-orient: vertical;
  }
}
</style>